body {
    padding: 0;
    margin: 0;
    column-count: 1;
}

p {
    font-family: 'Open Sans', sans-serif;
}

img {
    width: 250px;
    height: 250px;
}

h1,
h2,
h3,
h4,
h5 {
    font-family: 'Roboto Mono', monospace;
}

.NavBar {
    background-color: #262626;
    height: 7rem;
}

.App {}

.Presentacion {
    margin: 1rem;
    height: 20rem;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    flex-direction: column;
    background-color: #212529;
    padding: 0;
}

.Presentacion h3 {
    font-size: 4rem;
    padding: 0;
    margin: 0;
}

.Presentacion p {
    width: 50%;
    text-align: center;
}

.Componentes {
    display: grid;
    grid-template-columns: 25% 25% 25% 25%;
}

.pc {
    margin: 1rem;
    color: white;
    text-align: center;
}

.pc img {
    padding: .5rem;
    border-top-left-radius: 2rem;
    border-top-right-radius: 2rem;
    background-color: white;
    margin-bottom: -10px;
}

.pc a {
    color: white;
    margin: 1rem;
    font-size: 3rem;
}

.Color1 {
    background-color: #212529;
}

.Color2 {
    background-color: #F8F9FA;
}

.Color3 {
    background-color: #0D6EFD;
}

.PcPagina {
    padding-top: 3rem;
    padding-bottom: 3rem;
    display: flex;
    justify-content: center;
}

.PcPaginaContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 30%;
    border-top-left-radius: 5rem;
    border-top-right-radius: 5rem;
}

.PcPaginaContainer h3 {
    font-size: 4rem;
    margin: 1rem;
}

.PcPaginaContainer h3,
.PcPaginaContainer h4 {
    color: white;
}

.PcPaginaContainer h4 {
    font-size: 2.5rem;
    margin: 0;
    margin-top: .5rem;
    margin-bottom: .5rem;
}

.PcPaginaContainer .Dato {
    display: flex;
    flex-direction: row;
    justify-content: center;
    color: white;
}

.PcPaginaContainer .Dato p {
    margin: .5rem;
    font-size: 1.2rem;
}

.PcPaginaContainer .Dato p i {
    margin: .5rem;
}

.PcPaginaContainer a {
    font-size: 2rem;
    color: black;
}

.PcPaginaContainer .bottom {
    margin-bottom: 1rem;
}

@media (max-width: 1200px) {
    .pc img {
        width: 150px;
        height: 150px;
        margin-bottom: -17px;
    }

    .pc {
        padding: 1rem;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
    }

    .pc a {
        margin-top: 3rem;
    }

    .Componentes {
        display: grid;
        grid-template-columns: 50% 50%;
    }

    .PcPaginaContainer {
        width: 40%;
    }

    .PcPaginaContainer h3 {
        font-size: 3rem;
    }

    .PcPaginaContainer h4 {
        font-size: 2rem;
    }

    .PcPaginaContainer img {
        width: 200px;
        height: 200px;
    }

    .PcPaginaContainer .Dato p {
        font-size: 1rem;
    }
}

@media (max-width: 1000px) {
    .Presentacion h3 {
        font-size: 3rem;
        padding: 0;
        margin: 0;
    }

    .Presentacion p {
        width: 80%;
        text-align: center;
    }

    .PcPaginaContainer {
        width: 50%;
    }
}

@media (max-width: 750px) {
    .pc img {
        width: 150px;
        height: 150px;
        margin-bottom: -17px;
    }

    .pc {
        padding: 1rem;
        display: flex;
        flex-direction: row;
        justify-content: space-around;
    }

    .pc a {
        margin-top: 3rem;
    }

    .Componentes {
        display: grid;
        grid-template-columns: 100%;
    }
}

@media (max-width: 650px) {
    .Presentacion {
        height: 15rem;
    }

    .Presentacion h3 {
        font-size: 2.5rem;
    }

    .Presentacion p {
        font-size: .8rem;
    }

    .PcPaginaContainer {
        width: 70%;
    }
}

@media (max-width: 450px) {
    .Presentacion h3 {
        font-size: 2rem;
        padding: 0;
        margin: 0;
    }

    .Presentacion p {
        font-size: .7rem;
        width: 80%;
        text-align: center;
    }

    .PcPaginaContainer {}

    .PcPaginaContainer h3 {
        font-size: 2rem;
    }

    .PcPaginaContainer h4 {
        font-size: 1.8rem;
    }

    .PcPaginaContainer img {
        width: 150px;
        height: 150px;
    }

    .PcPaginaContainer .Dato p {
        font-size: .8rem;
    }
}